module.exports = "import { Component } from '@angular/core';\n\n@Component({\n  selector: 'nz-demo-tabs-icon',\n  template: `\n    <nz-tabset>\n      <nz-tab *ngFor=\"let tab of tabs\" [nzTitle]=\"titleTemplate\">\n        <ng-template #titleTemplate>\n          <i nz-icon [type]=\"tab.icon\"></i>{{ tab.name }}\n        </ng-template>\n        {{ tab.name }}\n      </nz-tab>\n    </nz-tabset>`\n})\nexport class NzDemoTabsIconComponent {\n  tabs = [\n    {\n      active: true,\n      name  : 'Tab 1',\n      icon  : 'apple'\n    },\n    {\n      active: false,\n      name  : 'Tab 2',\n      icon  : 'android'\n    }\n  ];\n}\n"